$gap : 1px;
$calendarMonthDays: 31;
$calendarDaysSize: 100px;
$paddings: 10px;

$days:
  "mon" 0,
  "tue" $calendarDaysSize + $gap,
  "wed" ($calendarDaysSize + $gap) * 2,
  "thu" ($calendarDaysSize + $gap) * 3,
  "fri" ($calendarDaysSize + $gap) * 4,
  "sat" ($calendarDaysSize + $gap) * 5,
  "sun" ($calendarDaysSize + $gap) * 6;
